<template>
	<div>
		<h1 class="title"></h1>
		<div>
			<el-button class="go-back" @click="goBack"></el-button>
		</div>
		<div>
			<div class="prism-player" id="J_prismPlayer"></div>
		</div>
	</div>
</template>
<script>
	export default {
		name: "a-li-yun",
		data() {
			return {
				player: null
			};
		},
		mounted() {
			this.init();
		},
		methods: {
			goBack() {
				history.back();
			},
			// 
			init() {
				this.player = new Aliplayer(
					{
						id: "J_prismPlayer", // 
						width: "96%", // 
						height: "700px", // 
						videoWidth: "100%", // 
						videoHeight: "85%", // 
						language: "zh-cn",
						autoplay: false, // 
						isLive: false, // 
						rePlay: true, // 
						useH5Prism: true, // H5
						// useFlashPrism:false, // Flash
						playsinline: true, // H5Android
						showBuffer: true, // true
						controlBarVisibility: "hover", // hoverclickhoveralwaysnever
						showBarTime: "", // 
						mediaType: "video", //vidvideovideoaudioMP4H5
						/**
						 * definition,FD,LDvid
						 * FD
						 * LD
						 * SD
						 * HD
						 * OD
						 * 2K2K
						 * 4K4K
						 */
						definition: "FD,LD,SD,HD",
						defaultDefinition: "FD",
						x5_video_position: "center",
						// 
						// source : 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4',
						source: JSON.stringify({
							HD: "//player.alicdn.com/resource/player/qupai.mp4",
							SD: "//player.alicdn.com/video/editor.mp4"
						}),
						// 
						vid: "1e067a2831b641db90d570b6480fbc40",
						playauth: "ddd", //
						cover:
							"http://liveroom-img.oss-cn-qingdao.aliyuncs.com/logo.png",
						// encryptType: 1, //
						// MPS
						// vid : '1e067a2831b641db90d570b6480fbc40', // ID
						// accId: 'dd',
						// accSecret: 'dd',
						// stsToken: 'dd',
						// domainRegion: 'dd',
						// authInfo: 'dd',
						// STS
						// vid : '1e067a2831b641db90d570b6480fbc40',
						// accessKeyId: 'dd',
						// securityToken: 'dd',
						// accessKeySecret: 'dd',
						// region: 'cn-shanghai', // eu-central-1, ap-southeast-1,
						// DRM
						// isDrm: true,
						// vid: '34ecdedd081c426383cd66782de6656d',
						// accessKeyId: 'dd',
						// securityToken: 'dd',
						// accessKeySecret: 'dd',
						// region: 'cn-shanghai', // eu-central-1, ap-southeast-1,
						// certId: 'dd', // 
						// components: [
						// 	{
						// 		name: "QualityComponent",
						// 		type: AliPlayerComponent.QualityComponent
						// 	}
						// ]
					},
					player => {
						console.log("The player is created.");
                        player.on('play',()=>{
                            console.log(111111111111)
                        })
					}
				);
			}
		}
	};
</script>
<style scoped lang="scss">
	.prism-player {
		margin: 0 auto;
		// height: 500px;
		border: 1px solid #ccc;
		background: #000;
		position: relative;
		color: #fff;
	}
</style>



