import Vue from "vue";
import messageComponent from "./index.vue";

// Vue.extend(),  
const messageConstructor = Vue.extend(messageComponent);

let instance;
let instances = [];
let seed = 1;
/**
 * @param {string} id id
 * @param {string} message 
 * @param {string} type  info:success:errorwarning:
 * @param {string} iconClass type
 * @param {string} customClass 
 * @param {boolean} center 
 * @param {boolean} showClose 
 * @param {boolean} dangerouslyUseHTMLString HTML
 * @param {number} offset toast 
 */

const Toast = (options) => {
	options = options || {};
	if (typeof options === "string") {
		// 
		options = {
			message: options
		};
	}

	let id = "my_message_" + seed++; // id

	let userOnClose = options.onClose;  //userOnClose,  message 

	options.onClose = function () {
		Toast.close(id, userOnClose);
	};

	// message,$el dom
	instance = new messageConstructor({
		data: options
	});

	instance.id = id; // id

	instance.$mount();

	document.body.appendChild(instance.$el); // toast

	let verticalOffset = options.offset || 20; // 

	instances.forEach((item) => {
		verticalOffset += item.$el.offsetHeight + 16; //  
	});

	instance.verticalOffset = verticalOffset;

	instance.visible = true; // 

	instance.$el.style.zIndex += seed; // z-index

	instances.push(instance);
	return instance;
};

// Message
// this.$message.success('xxx')this.$message({type: 'success',message: 'xxx'})
["success", "warning", "info", "error"].forEach((type) => {
	Toast[type] = (options) => {
		if (typeof options === "string") {
			// 
			options = {
				message: options
			};
		}
		options.type = type;
		return Toast(options);
	};
});

Toast.close = function (id, userOnClose) {
	let len = instances.length;
	let index = -1;
	let removedHeight;
	for (let i = 0; i < len; i++) {
		if (id === instances[i].id) {
			removedHeight = instances[i].$el.offsetHeight;
			index = i;
			if (typeof userOnClose === "function") {
				userOnClose(instances[i]);
			}
			instances.splice(i, 1);
			break;
		}
	}
	if (len <= 1 || index === -1 || index > instances.length - 1) return;
	for (let i = index; i < len - 1; i++) {
		let dom = instances[i].$el;
		dom.style["top"] = parseInt(dom.style["top"], 10) - removedHeight - 16 + "px";
	}
};

// 
Toast.closeAll = function () {
	for (let i = instances.length - 1; i >= 0; i--) {
		instances[i].close();
	}
};

function toast() {
	window.toast = Vue.prototype.$toast = Toast; // 
}
export default toast;



